import { AfterContentInit, Component } from '@angular/core';
import { ControlContainer, FormGroup } from '@angular/forms';
import { states } from '../../constants';
import { BillpayUtilityService as Utility } from '../../services/billpay-utility.service';

@Component({
  selector: 'bb-billpay-address-form',
  templateUrl: './billpay-address-form.component.html',
})
export class BillpayAddressFormComponent implements AfterContentInit {
  readonly states = states;
  formGroup?: FormGroup;
  readonly address1RequiredId = Utility.generateId();
  readonly address1MaxLengthId = Utility.generateId();
  readonly address2MaxLengthId = Utility.generateId();
  readonly cityRequiredId = Utility.generateId();
  readonly cityMaxlengthId = Utility.generateId();
  readonly stateLengthId = Utility.generateId();
  readonly postalCodeRequiredId = Utility.generateId();
  readonly postalCodePatternId = Utility.generateId();
  readonly labelId = Utility.generateId();

  /** @internal */
  constructor(private readonly controlContainer: ControlContainer) {}

  ngAfterContentInit() {
    this.formGroup = this.controlContainer.control as FormGroup;
  }

  hasError(controlName: string, errorName: string): boolean {
    const control = this.formGroup && this.formGroup.get(controlName);
    return !!control && control.hasError(errorName) && control.touched;
  }
}
